.container {
  display: flex;
  flex-direction: column;
}

.schedule {
  margin-bottom: 20px;
}

.day {
  display: flex;
  justify-content: space-between;
  padding: 5px 20px;
  margin-left: -20px;
  margin-right: -20px;
}

.shadedDay {
  background-color: #F6F6F6;
}

.date {
  margin-right: 20px;
  font-weight: bold;
  font-size: 16px;
}

.staff {
  font-size: 16px;
  text-align: right;
}

.score {
  display: flex;
  margin-bottom: 10px;
  justify-content: space-between;
  align-items: center;
}

.scoreHeader {
  margin-bottom: 10px;
  font-size: 22px;
  font-weight: bold;
  font-family: 'Secular One', sans-serif;
}

.scoreTitle {
  font-weight: bold;
  font-size: 16px;
  margin-right: 20px;
}

.scoreValue {
  font-size: 16px;
}

.download {
  padding: 10px;
  background-color: var(--color-red);
  color: white;
  border-radius: 30px;
  padding: 7px 10px;
  font-family: 'Secular One', sans-serif;
  font-size: 16px;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  margin-top: 20px;
}

.download:hover {
  opacity: 0.5;
}