.container {
  display: flex;
  flex-wrap: wrap;
}

.person {
  background-color: var(--color-blue);
  border-radius: 10px;
  padding: 10px;
  margin-right: 10px;
  margin-bottom: 10px;
}

.name {
  font-family: 'Secular One', sans-serif;
  font-size: 18px;
  margin-bottom: 10px;
  font-weight: bold ;
}

.attribute {
  margin-bottom: 5px;
}

.attributeTitle {
  font-weight: bold;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  margin-bottom: 5px;
}

.attributeItem {
  background-color: white;
  opacity: 0.85;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
  margin-bottom: 5px;
}