.container {
  display: flex;
  flex-direction: column;
}

.schedule {
  margin-bottom: 20px;
}

.day {
  display: flex;
  padding: 5px 20px;
  margin-left: -20px;
  margin-right: -20px;
}

.shadedDay {
  background-color: #F6F6F6;
}

.date {
  margin-right: 20px;
  font-weight: bold;
  font-size: 16px;
}

.jobs {
  display: flex;
  flex-wrap: wrap;
}

.job {
  background-color: var(--color-blue);
  border-radius: 10px;
  padding: 10px;
  margin-right: 10px;
  margin-bottom: 10px;
}

.green {
  background-color: #C6FFCD;
}

.red {
  background-color: #FFC7C6;
}

.jobTitle {
  font-weight: bold;
  font-size: 14px;
  display: flex;
  flex-direction: column;
}

.staff {
  display: flex;
  flex-direction: column;
}

.person {
  opacity: 0.85;
  border-radius: 5px;
  font-size: 12px;
}