.arrow {
  position: absolute;
  top: 9px;
  right: 15px;
  transform: rotate(90deg) scaleY(1.5);
  font-size: 14px;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  cursor: pointer;
  padding: 20px;
  user-select: none;
}

.arrow:hover {
  opacity: 0.5;
}

.placeholder {
  width: 300px;
}

.section {
  background-color: var(--color-blue);
  border-radius: 10px;
  padding: 10px;
  margin-right: 10px;
  margin-bottom: 10px;
}

.title {
  font-family: 'Secular One', sans-serif;
  font-size: 18px;
  margin-bottom: 10px;
  font-weight: bold ;
}

.formula {
  background-color: white;
  opacity: 0.85;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
  margin-bottom: 5px;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
}

.input {
  width: 100px;
  font-size: 14px;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  margin: 2px;
}
