@import url('https://fonts.googleapis.com/css2?family=Open+Sans&family=Secular+One&family=Sigmar+One&display=swap');

html {
  --color-bg: #E5F6FE;
  --color-purple: #473B84;
  --color-green: #AFC4A8;
  --color-yellow: #FFDC76;
  --color-red: #DB6562;
  --color-blue: #CBE2FF;
}

body {
  margin: 0;
  padding: 0 0 20px 0;
  background-color: var(--color-bg);
  font-family: 'Open Sans', sans-serif;
}